'use client';

import React, { useState, useEffect } from 'react';
import { Calendar, Clock, CheckCircle, XCircle, Trash2, Filter } from 'lucide-react';
import { questOperations, Quest } from '../lib/supabase';

interface QuestsListProps {
  refreshTrigger?: number;
}

export function QuestsList({ refreshTrigger }: QuestsListProps) {
  const [quests, setQuests] = useState<Quest[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [filter, setFilter] = useState<'all' | 'daily' | 'weekly' | 'one_time'>('all');
  const [showCompleted, setShowCompleted] = useState(true);

  const fetchQuests = async () => {
    try {
      setLoading(true);
      setError(null);
      const fetchedQuests = await questOperations.getQuests();
      setQuests(fetchedQuests);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to fetch quests');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchQuests();
  }, [refreshTrigger]);

  const handleToggleComplete = async (questId: string, currentStatus: boolean) => {
    try {
      await questOperations.updateQuest(questId, { completed: !currentStatus });
      setQuests(quests.map(quest => 
        quest.id === questId ? { ...quest, completed: !currentStatus } : quest
      ));
    } catch (err) {
      setError('Failed to update quest status');
    }
  };

  const handleDeleteQuest = async (questId: string) => {
    if (!confirm('Are you sure you want to delete this quest?')) return;
    
    try {
      await questOperations.deleteQuest(questId);
      setQuests(quests.filter(quest => quest.id !== questId));
    } catch (err) {
      setError('Failed to delete quest');
    }
  };

  const filteredQuests = quests.filter(quest => {
    if (filter !== 'all' && quest.type !== filter) return false;
    if (!showCompleted && quest.completed) return false;
    return true;
  });

  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleDateString() + ' ' + date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
  };

  const isOverdue = (dateString: string) => {
    return new Date(dateString) < new Date();
  };

  const getTypeColor = (type: string) => {
    switch (type) {
      case 'daily': return 'bg-blue-100 text-blue-800';
      case 'weekly': return 'bg-green-100 text-green-800';
      case 'one_time': return 'bg-purple-100 text-purple-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  if (loading) {
    return (
      <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
        <div className="animate-pulse">
          <div className="h-6 bg-gray-200 rounded mb-4"></div>
          <div className="space-y-3">
            {[1, 2, 3].map(i => (
              <div key={i} className="h-20 bg-gray-200 rounded"></div>
            ))}
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="bg-white rounded-xl shadow-sm border border-gray-200">
      {/* Header */}
      <div className="px-6 py-4 border-b border-gray-200">
        <div className="flex items-center justify-between mb-4">
          <h2 className="text-xl font-semibold text-gray-900">My Quests</h2>
          <span className="text-sm text-gray-500">{filteredQuests.length} quest{filteredQuests.length !== 1 ? 's' : ''}</span>
        </div>
        
        {/* Filters */}
        <div className="flex flex-wrap gap-2">
          <div className="flex items-center gap-2">
            <Filter className="w-4 h-4 text-gray-500" />
            <select
              value={filter}
              onChange={(e) => setFilter(e.target.value as any)}
              className="text-sm border border-gray-300 rounded px-2 py-1 focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <option value="all">All Types</option>
              <option value="daily">Daily</option>
              <option value="weekly">Weekly</option>
              <option value="one_time">One-time</option>
            </select>
          </div>
          <label className="flex items-center gap-2 text-sm">
            <input
              type="checkbox"
              checked={showCompleted}
              onChange={(e) => setShowCompleted(e.target.checked)}
              className="rounded border-gray-300 focus:ring-blue-500"
            />
            Show completed
          </label>
        </div>
      </div>

      {/* Error Message */}
      {error && (
        <div className="mx-6 mt-4 p-3 bg-red-50 border border-red-200 text-red-700 rounded-lg">
          {error}
        </div>
      )}

      {/* Quests List */}
      <div className="p-6">
        {filteredQuests.length === 0 ? (
          <div className="text-center py-8 text-gray-500">
            <Calendar className="w-12 h-12 mx-auto mb-3 text-gray-300" />
            <p>No quests found</p>
            <p className="text-sm">Create your first quest to get started!</p>
          </div>
        ) : (
          <div className="space-y-3">
            {filteredQuests.map((quest) => (
              <div
                key={quest.id}
                className={`p-4 border rounded-lg transition-colors ${
                  quest.completed 
                    ? 'bg-gray-50 border-gray-200' 
                    : isOverdue(quest.due_date) 
                      ? 'bg-red-50 border-red-200' 
                      : 'bg-white border-gray-200 hover:border-gray-300'
                }`}
              >
                <div className="flex items-start justify-between">
                  <div className="flex-1">
                    <div className="flex items-center gap-3 mb-2">
                      <button
                        onClick={() => handleToggleComplete(quest.id, quest.completed)}
                        className={`flex-shrink-0 w-5 h-5 rounded-full border-2 flex items-center justify-center transition-colors ${
                          quest.completed
                            ? 'bg-green-500 border-green-500 text-white'
                            : 'border-gray-300 hover:border-green-500'
                        }`}
                      >
                        {quest.completed && <CheckCircle className="w-3 h-3" />}
                      </button>
                      <h3 className={`font-medium ${
                        quest.completed ? 'text-gray-500 line-through' : 'text-gray-900'
                      }`}>
                        {quest.title}
                      </h3>
                      <span className={`px-2 py-1 text-xs font-medium rounded-full ${getTypeColor(quest.type)}`}>
                        {quest.type.replace('_', ' ')}
                      </span>
                    </div>
                    
                    {quest.description && (
                      <p className={`text-sm mb-2 ${
                        quest.completed ? 'text-gray-400' : 'text-gray-600'
                      }`}>
                        {quest.description}
                      </p>
                    )}
                    
                    <div className="flex items-center gap-4 text-xs text-gray-500">
                      <div className="flex items-center gap-1">
                        <Clock className="w-3 h-3" />
                        <span className={isOverdue(quest.due_date) && !quest.completed ? 'text-red-600 font-medium' : ''}>
                          Due: {formatDate(quest.due_date)}
                        </span>
                        {isOverdue(quest.due_date) && !quest.completed && (
                          <span className="text-red-600 font-medium">(Overdue)</span>
                        )}
                      </div>
                    </div>
                  </div>
                  
                  <button
                    onClick={() => handleDeleteQuest(quest.id)}
                    className="flex-shrink-0 p-1 text-gray-400 hover:text-red-500 transition-colors"
                    title="Delete quest"
                  >
                    <Trash2 className="w-4 h-4" />
                  </button>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}