export const supabase = createClient(supabaseUrl, supabaseAnonKey);

// Database types
export interface Quest {
  id?: string;
  title: string;
  description?: string;
  type: 'daily' | 'weekly' | 'one_time';
  due_date: string;
  created_at?: string;
  user_id?: string;
  completed?: boolean;
}

// Database operations
export const questOperations = {
  // Create a new quest
  async createQuest(quest: Omit<Quest, 'id' | 'created_at' | 'user_id'>) {
    const { data: { user } } = await supabase.auth.getUser();
    
    const { data, error } = await supabase
      .from('quests')
      .insert({
        ...quest,
        user_id: user?.id
      })
      .select()
      .single();
    
    if (error) throw error;
    return data;
  },

  // Fetch all quests for the current user
  async getQuests() {
    const { data: { user } } = await supabase.auth.getUser();
    
    const { data, error } = await supabase
      .from('quests')
      .select('*')
      .eq('user_id', user?.id)
      .order('created_at', { ascending: false });
    
    if (error) throw error;
    return data;
  },

  // Update a quest
  async updateQuest(id: string, updates: Partial<Quest>) {
    const { data, error } = await supabase
      .from('quests')
      .update(updates)
      .eq('id', id)
      .select()
      .single();
    
    if (error) throw error;
    return data;
  },

  // Delete a quest
  async deleteQuest(id: string) {
    const { error } = await supabase
      .from('quests')
      .delete()
      .eq('id', id);
    
    if (error) throw error;
  }
};

// Auth operations
export const authOperations = {
  // Sign up
  async signUp(email: string, password: string) {
    const { data, error } = await supabase.auth.signUp({
      email,
      password
    });
    
    if (error) throw error;
    return data;
  },

  // Sign in
  async signIn(email: string, password: string) {
    const { data, error } = await supabase.auth.signInWithPassword({
      email,
      password
    });
    
    if (error) throw error;
    return data;
  },

  // Sign out
  async signOut() {
    const { error } = await supabase.auth.signOut();
    if (error) throw error;
  },

  // Get current user
  async getCurrentUser() {
    const { data: { user } } = await supabase.auth.getUser();
    return user;
  }
};