'use client';

import React, { useState } from 'react';
import { PlusCircle, Loader2 } from 'lucide-react';
import { Field } from './Field';
import { questOperations, authOperations } from '../lib/supabase';

interface CreateQuestCardProps {
  onQuestCreated?: () => void;
}

export function CreateQuestCard({ onQuestCreated }: CreateQuestCardProps) {
  const [formData, setFormData] = useState({
    title: '',
    description: '',
    type: 'daily' as 'daily' | 'weekly' | 'one_time',
    dueDateTime: ''
  });
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleCreateQuest = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);
    setError(null);
    setSuccess(null);

    try {
      // Check if user is authenticated
      const user = await authOperations.getCurrentUser();
      if (!user) {
        throw new Error('Please log in to create quests');
      }

      // Validate form data
      if (!formData.title.trim()) {
        throw new Error('Title is required');
      }
      if (!formData.dueDateTime) {
        throw new Error('Due date and time is required');
      }

      // Create quest in database
      await questOperations.createQuest({
        title: formData.title.trim(),
        description: formData.description.trim() || undefined,
        type: formData.type,
        due_date: formData.dueDateTime
      });

      setSuccess('Quest created successfully!');
      handleClear();
      onQuestCreated?.();
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to create quest');
    } finally {
      setIsLoading(false);
    }
  };

  const handleClear = () => {
    setFormData({
      title: '',
      description: '',
      type: 'daily',
      dueDateTime: ''
    });
  };

  const handleLogout = async () => {
    try {
      await authOperations.signOut();
      setSuccess('Logged out successfully');
    } catch (err) {
      setError('Failed to log out');
    }
  };

  return (
    <div className="w-full max-w-xl rounded-2xl border border-slate-200 bg-white shadow-lg">
      {/* Header */}
      <div className="px-6 pt-6">
        <h2 className="text-2xl font-bold bg-gradient-to-r from-sky-500 to-emerald-400 bg-clip-text text-transparent">
          QuestBoard
        </h2>
        <div className="mt-4 mb-6 h-px bg-slate-200"></div>
        <button className="inline-flex items-center gap-2 rounded-full border border-sky-200 bg-sky-50 px-4 py-2 text-sm font-medium text-sky-700 transition hover:bg-sky-100">
          <PlusCircle className="h-4 w-4" />
          Create Quest
        </button>
      </div>

      {/* Form */}
      <form onSubmit={handleCreateQuest} className="px-6 pt-6 pb-6">
        {/* Error Message */}
        {error && (
          <div className="mb-4 p-3 bg-red-50 border border-red-200 text-red-700 rounded-lg">
            {error}
          </div>
        )}
        
        {/* Success Message */}
        {success && (
          <div className="mb-4 p-3 bg-green-50 border border-green-200 text-green-700 rounded-lg">
            {success}
          </div>
        )}

        {/* Title Field */}
        <Field label="Title *">
          <input
            type="text"
            name="title"
            value={formData.title}
            onChange={handleInputChange}
            required
            disabled={isLoading}
            className="w-full rounded-lg border border-slate-200 bg-white px-3 py-2 text-slate-800 outline-none transition focus:border-sky-300 focus:shadow-[0_0_0_3px] focus:shadow-sky-100 disabled:opacity-50 disabled:cursor-not-allowed"
            placeholder="Enter quest title"
          />
        </Field>

        {/* Description Field */}
        <Field label="Description">
          <textarea
            name="description"
            value={formData.description}
            onChange={handleInputChange}
            rows={3}
            disabled={isLoading}
            className="w-full rounded-lg border border-slate-200 bg-white px-3 py-2 text-slate-800 outline-none transition focus:border-sky-300 focus:shadow-[0_0_0_3px] focus:shadow-sky-100 resize-none disabled:opacity-50 disabled:cursor-not-allowed"
            placeholder="Enter quest description"
          />
        </Field>

        {/* Type and Due Date Grid */}
        <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
          {/* Type Field */}
          <Field label="Type *">
            <select
              name="type"
              value={formData.type}
              onChange={handleInputChange}
              required
              disabled={isLoading}
              className="w-full rounded-lg border border-slate-200 bg-white px-3 py-2 text-slate-800 outline-none transition focus:border-sky-300 focus:shadow-[0_0_0_3px] focus:shadow-sky-100 disabled:opacity-50 disabled:cursor-not-allowed"
            >
              <option value="daily">Daily</option>
              <option value="weekly">Weekly</option>
              <option value="one_time">One Time</option>
            </select>
          </Field>

          {/* Due Date & Time Field */}
          <Field label="Due Date & Time *">
            <input
              type="datetime-local"
              name="dueDateTime"
              value={formData.dueDateTime}
              onChange={handleInputChange}
              required
              disabled={isLoading}
              className="w-full rounded-lg border border-slate-200 bg-white px-3 py-2 text-slate-800 outline-none transition focus:border-sky-300 focus:shadow-[0_0_0_3px] focus:shadow-sky-100 disabled:opacity-50 disabled:cursor-not-allowed"
            />
          </Field>
        </div>

        {/* Action Buttons */}
        <div className="flex gap-3 mt-6">
          <button
            type="submit"
            disabled={isLoading}
            className="inline-flex items-center justify-center rounded-lg border-0 bg-gradient-to-r from-sky-500 to-emerald-400 px-4 py-2 font-medium text-slate-900 shadow-sm transition hover:opacity-90 focus:outline-none focus:ring-2 focus:ring-sky-300 focus:ring-offset-2 disabled:opacity-50 disabled:cursor-not-allowed"
          >
            {isLoading ? (
              <>
                <Loader2 className="h-4 w-4 animate-spin mr-2" />
                Creating...
              </>
            ) : (
              'Create Quest'
            )}
          </button>
          <button
            type="button"
            onClick={handleClear}
            disabled={isLoading}
            className="inline-flex items-center justify-center rounded-lg border border-slate-200 bg-white px-4 py-2 font-medium text-slate-700 shadow-sm hover:bg-slate-50 focus:outline-none focus:ring-2 focus:ring-sky-300 focus:ring-offset-2 disabled:opacity-50 disabled:cursor-not-allowed"
          >
            Clear
          </button>
        </div>

        {/* Logout Button */}
        <button
          type="button"
          onClick={handleLogout}
          className="mt-4 w-full inline-flex items-center justify-center rounded-lg border border-slate-200 bg-white px-4 py-2 font-medium text-slate-700 shadow-sm hover:bg-slate-50 focus:outline-none focus:ring-2 focus:ring-sky-300 focus:ring-offset-2"
        >
          Logout
        </button>
      </form>
    </div>
  );
}